} from "@vue/runtime-core";

import { ICard } from "../interfaces/interfaces";

interface ISetup {
  flipCard: () => void;
  cardRef: Ref<HTMLDivElement | undefined>;
  cardFontSize: Ref<string>;
}

const __default__ = defineComponent({
  name: "Card",

  props: {
