
import {
  computed,
  ComputedRef,
  defineComponent,
  SetupContext
} from "@vue/runtime-core";

interface ISetup {
  stepsLabel: ComputedRef<string>;
  replay: () => void;
}

export default defineComponent({
  name: "Victory",

  props: {
    steps: {
      type: Number,
      required: true
    }
  },

  emits: ["replay"],

  setup(props, { emit }: SetupContext): ISetup {
    const stepsLabel = computed<string>(() =>
      props.steps === 1 ? "step" : "steps"
    );

    function replay(): void {
      emit("replay");
    }

    return {
      stepsLabel,
      replay
    };
  }
});
